import React, { useEffect } from 'react';
import css from '../components/phonebook.module.css';
import ContactForm from '../components/ContactForm.jsx';
import ContactList from '../components/ContactList.jsx';
import Filter from '../components/Filter';
import { useSelector, useDispatch } from 'react-redux';
import {
  addContact,
  deleteContact,
  fetchContacts,
} from '../redux/actions.js';
import { setFilter } from '../redux/reducers.js';

function ContactsPage() {
//za pomoc hooka 'useSelector' pobieram fragmenty stanu z store'a, czyli list kontaktw (contacts) i filtr (filter), ktry bdzie uywany do filtrowania kontaktw
//za pomoc hooka 'useDispatch' pobieram funkcj 'dispatch', ktra pozwala na wysyanie akcji do store'a
  const contacts = useSelector(state => state.contacts);
  const filter = useSelector(state => state.filter);
  const dispatch = useDispatch();

//na podstawie zalenoci [dispatch] apka sprawdza, czy s zapisane kontakty w localStorage. Jeli s zapisane, s one parsowane z localStorage i wysyane do store'a za pomoc akcji 'addContact' 
  useEffect(() => {
    dispatch(fetchContacts());
  }, [dispatch]);

  //ta funkcja jest wywoywana w momencie przesania formularza z danymi kontaktu. Wysya akcj 'addContact' do store'a z nowym kontaktem
   const handleSubmit = contact => {
    dispatch(addContact(contact));
  };

  //ta funkcja jest przekazywana do komponentu ContactList jako callback przy usuwaniu kontaktu.Po klikniciu przycisku usuwania, funkcja wysya akcj 'deleteContact' z identyfikatorem usuwanego kontaktu
      const handleDeleteContact = id => {
    dispatch(deleteContact(id));
  };

  // ta funkcja jest wywoywana przy zmianie wartoci pola filtru. W tej funkcji warto pola jest wysyana za pomoc akcji 'setFilter' do store'a
    const handleFilterChange = event => {
    dispatch(setFilter(event.target.value.toLowerCase()));
  };

  // wyfiltrowane kontakty s przekazywane do komponentu 'ContactList' jako waciwo contacts
      const filteredContacts = contacts.filter(contact =>
    contact.name?.toLowerCase().includes(filter)
  );

  return (
    <div className="App">
      <div className={css['container']}>
        <h1>Phonebook</h1>
        <ContactForm contacts={contacts} handleSubmit={handleSubmit} />
        <h2>Contacts</h2>
        <Filter filter={filter} handleFilterChange={handleFilterChange} />
        <ContactList
          contacts={filteredContacts}
          onDeleteContact={handleDeleteContact}
        />
      </div>
    </div>
  );
}

export default ContactsPage;

//strona, na ktrej wywietlane s kontakty uytkownika. Komunikuje si z Redux, aby pobra i wywietli list kontaktw.